<template>
    <div>
      <el-button type="danger" @click='createLabelData'>生成标注数据</el-button>
      <div v-for='bratData in bratDatas'>
        <malgo-brat v-bind:brat-data='bratData.bratData' v-bind:brat-config-data='bratConfig' v-bind:brat-id='bratData.id'></malgo-brat>
      </div>
    </div>
</template>

<script type="text/javascript">
    import MalgoBrat from '@/components/MalgoBrat.vue'
    export default {
      data () {
        return {
          // annos: null,
          bratDatas:null,
          bratConfig:null
        }
      },
      created:function(){

      },
      methods:{
            createLabelData(){
              var self=this
              self.getAllDataByFetch()
            },
            getAllDataByFetch(){
              var self=this
    
              let formData=new FormData();
              formData.append('userId', "11111111111111111111");
              formData.append('pageNum', "1");
              formData.append('pageSize', "10");
              console.log(formData)
              //http://192.168.31.126:8080/annotation/config.do
              fetch('http://annotation.malgo.cn/static/json/config.json',{})
                .then(function(response){
                  return response.json()
                })
                .then(function(response){
                    self.bratConfig=response;
                    fetch('http://annotation.malgo.cn/annotation/list.do',
                      {method: 'POST',
                      body:formData
                    })
                      .then(function(response){
                        return response.json()
                      })
                      .then(function(response){
                        self.bratDatas=response.data.dataList;
                      });
                 });
            },
            getAllData(){
                 axios.get('http://annotation.malgo.cn/static/json/config.json')
                    .then(function(response){
                      self.bratConfig=response.data
                      console.log(response);
                    }).then(function(){
                      axios.post('http://annotation.malgo.cn/annotation/list.do',qs.stringify({
                        userId:"11111111111111111111",
                        pageNum:"1",
                        state:"",
                        pageSize:"10"}),{
                          headers:{
                            'Content-Type':'multipart/form-data'
                          }
                        })
                          .then(function(response){
                            self.bratDatas=response.data.brat;
                            console.log(response);
                        })
                    })
                    .catch(function(err){
                      console.log(err);
                    });
            },
            getBratData(){
              var self=this
                 axios.post('http://192.168.31.126:8090/annotation/list.do',{
                  userId:"11111111111111111111",
                  pageNum:"1",
                  pageSize:"10"})
                    .then(function(response){
                      self.bratDatas=response.data.brat;
                      console.log(response);
                    })
                    .catch(function(err){
                      console.log(err);
                    });
            },
            getBratConfig(){
              var self=this
              axios.get('http://192.168.31.126:8090/static/json/config.json')
                    .then(function(response){
                      self.bratConfig=response.data
                      console.log(response);
                    })
                    .catch(function(err){
                      console.log(err);
                    });
            }
      },
      components: {
        'malgo-brat': MalgoBrat
      }
    }
</script>


