<template>
    <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" style="width:350px;margin: 180px auto;">
      <h3 class="title">系统登录</h3>
      <el-form-item  prop="account">
        <el-input v-model="ruleForm2.account" auto-complete="off" placeholder="账号"></el-input>
      </el-form-item>
      <el-form-item  prop="checkPass">
        <el-input type="password"v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <el-checkbox v-model="checked" checked class="remeber">记住密码</el-checkbox>
      <el-form-item style="width:100%">
        <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
      </el-form-item>
    </el-form>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      logining: false,
      ruleForm2: {
        account:null,
        checkPass:null
      },
      rules2: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' },
        ],
        checkPass: [
          { required: true, message: '请输入密码', trigger: 'blur' },
        ]
      },
      checked: true
    }
  },
  methods:{
    dispathcerPage(data){
      sessionStorage.setItem('user', JSON.stringify(data));
      this.$router.push({ path: '/mainPage' });
    },
    handleSubmit2(ev) {
         var self = this;
      self.$refs.ruleForm2.validate((valid) => {
        if(valid){
          self.logining = true;
          let formData=new FormData();
          formData.append("accountNo",self.ruleForm2.account);
          formData.append("loginPwd",self.ruleForm2.checkPass);
          
          fetch('http://annotation.malgo.cn/auth/anonymous/login.do',{
            method: 'POST',
            body:formData
            ,
            credentials:'include'
          })
          .then(function(response){
            return response.json();
          })
          .then(function(response){
            if(response.succ==true||response.succ=="true")
            {
              console.log("成功登录后台："+response)
              // this.dispathcerPage(formData);
              // self.$router.push({ name: 'Login' });
              // location.assign('./index.html')
            }else{
              self.$message({
                message: response.msg,
                type: 'error'
              });
            }
          })
        }else{
          console.log('error submit!!');
          return false;
        }
      });
    }
  }
}
</script>
