import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'
// import TermAnno from '@/components/TermAnno'
// import DemoAnno from'@/components/DemoAnno'
import AnnoPage from '@/components/AnnoPage'
import Login from '@/components/Login'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/login',
      name:'Login',
      component:Login,
      hidden:true
    },
    {
      path:'/mainPage',
      name:'暂定为主页面',
      component:AnnoPage
      // redirect:'/login',
      // hidden:true
    }
    ,
    // {
    //   path: '/',
    //   name: 'Login',
    //   component: Login
    // },
    // {
    //   path: '/term_anno',
    //   name: 'TermAnno',
    //   component: TermAnno
    // },
    // {
    //   path:'/demo',
    //   name:'demoAnno',
    //   component:DemoAnno
    // },
    {
      path:'*',
      hidden:true,
      redirect:{path:'/login'}
    }
  ]
})



