<template>
  <el-dialog ref='term-detail' title="标注数据" :visible.sync='visible'>
    <el-form  label-position="left" :model="annotation" label-width="130px" size="mini">
      <el-form-item label="词条内容" size="mini">
        <el-input v-model="annotation.term" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="词条分类">
        <el-select v-model="annotation.type" placeholder="请选择" size="large" filterable>
          <el-option
            v-for="item in annotation.types"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            <span style="float: left">{{item.label}}</span>
            <span style="float:right;color:#8492a6; font-size: 13px">{{item.value}}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="同时标注相同词条">
        <el-switch
          v-model="annotation.isNewTerm"
          on-color="#13ce66"
          off-color="#ff4949">
        </el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="mySubmit">标注词条</el-button>
        <el-button @click='visible=false'>取消</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>
<script>
    export default {
      props: ['annotation', 'submit'],
      data () {
        return {
          visible: false
        };
      },
      methods: {
        open () {
          this.visible = true
        },
        mySubmit: function(){
          this.visible = false
          this.submit()
        }
      }
    }
</script>
<style type="text/css">
  .el-input.is-disabled .el-input__inner{
    background-color: #fbfdff;
  }
  .el-input.is-disabled{
    background-color: #fbfdff;
  }
  .el-select-dropdown__item{
    height:18px;
    padding-top:1px;
    padding-bottom:1px; 
  }
  .el-select-dropdown__wrap{
    max-height: 600px;
  }
  .el-select-dropdown{
    /*margin-top:30px;*/
  }
</style>


