<template>
  <div>
    <h2>标注文档</h2>
    <modal-term-detail ref='termDialog' v-bind:annotation='selectedTerm' v-bind:submit='submitTerm'/>
    <modal-delete-term ref='termDeleteDialog' v-bind:annotation='selectedTerm' v-bind:submit='submitDeleteTerm'/>
    <el-row>
      <el-col :span="19">
        <!-- <el-row>
            <el-col :span="24">
                 
            </el-col>
        </el-row> -->
        <el-row>
            <el-col :span="4">
                <el-button type="danger" @click='getAllData' icon="search">获取/刷新标注数据</el-button>
                <!-- <el-button type="danger" @click="testConfig">全部提交</el-button> -->
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
              <div id="mark-result">
                <el-table
                  v-loading="loading"
                  element-loading-text="加载数据"
                  :data="bratDatas"
                  @row-click="rowClick"
                  style="width:100%"  
                  empty-text="暂无数据"
                  highlight-current-row
                  >
                  <el-table-column label="ANID"  min-width="190"  header-align="center">
                    <template scope="brats">
                        <span>{{brats.row.id}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="标注状态" min-width="100" header-align="center">
                    <template scope="brats">
                      <div v-if="brats.row.state === 'PROCESSING'">
                        <div style="background-color:#EEEED1;border-radius: 3px;">标注中</div>
                      </div>
                      <div v-else-if="brats.row.state === 'INIT'">
                        <div style="background-color:#EEB422;border-radius: 3px;">未标注</div>
                      </div>
                      <div v-else>
                        待定
                      </div>
                    </template>
                  </el-table-column>
                  <el-table-column label="标注数据"  min-width="600" header-align="center" >
                    <template scope="brats">
                      <malgo-brat v-bind:brat-data='brats.row.bratData' 
                        :brat-config-data='bratConfig' 
                        :brat-id='brats.row.id'
                        :on-select-text='selectText'
                        :on-select-label='selectLabel'>
                      </malgo-brat>
                    </template>
                  </el-table-column>
                  <el-table-column label="操作"  min-width="180"
                    header-align="center">
                    <template  scope="scope">
                      <el-button type="primary" size="small" icon="circle-check"
                                 @click='commitAnnotation(scope.$index,scope.row.id)'>提交</el-button>
                      <el-button type="success" size="small" icon="time"
                                 @click='reportSuspious(scope.$index,scope.row.id)'>待定</el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </el-col>
        </el-row>
      </el-col>
      <el-col :span="5">
        <el-row>
          <el-col :span="8">
            
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">   
            <h4>新添加词条:</h4>
            <div style="">
              <ul style="border: #ddd 1px solid;
                        border-radius: 3px;
                        float: left;
                        width: 100%;
                        min-width:200px;
                        min-height: 200px;
                        padding-left: 2px;
                        padding-top: 2px;">
                <li style="background-color: #455a64;
                            color: #fff;
                            border-radius: 3px;
                            cursor: default;
                            margin-right: 2px;
                            margin-top: 2px;
                            padding: 7px 12px;"
                            v-for='addedWord in currentBratData.newTerms'>
                                  {{addedWord}}<i style="
                              cursor: pointer;
                              float: right;
                              font-size: 14px;
                              margin-top: 3px;
                              line-height: 1;
                              margin-left: 7px;
                              opacity: .75;
                  "class="el-icon-delete" @click="deleteNewTerm(addedWord, currentBratData.id)"></i>
                </li>
              </ul>
            </div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script type="text/javascript">
    import MalgoBrat from '@/components/MalgoBrat.vue'
    import ModalTermDetail from '@/components/ModalTermDetail.vue'
    import ModalDeleteTerm from '@/components/ModalDeleteTerm.vue'        
    export default {
      data () {
        return {
          createTime:Date.now,
          lastEditTime:Date.now,
          textarea:null,
          bratConfig:null,
          bratDatas:null,
          currentBratId:null,
          selectedTerm: {
            term: null,
            types: [],
            isAddTerm: false,
            startOffset: null,
            endOffset: null,
            type: null,
            tag:null
          },
          // addedWords:null,
          // isAddTerm:true,
          loading:false,
          currentBratData:{
            newTerms:[],
            id:null
          }
        }
      },
      created () {

      },
      methods:{
        rowClick(row){
          var self=this;
          self.currentBratData.newTerms=[];
          if(row.newTerms!=null){
            for(var i=0;i<row.newTerms.length;i++){
              var forArr=row.newTerms[i];
              self.currentBratData.newTerms.push(forArr);
            }
          } 
          self.currentBratData.id=row.id;
        },
        deleteNewTerm(currentAddedWord,currentBratId){//仅仅删除新添加词条框内对应的新词
          var self=this;
          var currentArrWord=currentAddedWord.split(" - ");
          let formDelete=new FormData();
          formDelete.append("anId",currentBratId);
          formDelete.append("term",currentArrWord[0]);
          formDelete.append("termType",currentArrWord[1]);
          //116.62.160.143:8080/
          //118.89.108.199:9090/
          fetch('http://annotation.malgo.cn/annotation/deleteNewTerm.do',{
            method: 'POST',
            body:formDelete
            ,
            credentials:'include'
          })
          .then(function(response){
            return response.json()
          })
          .then(function(response){
            if(response.succ=="false"||response.succ==false){
              alert("请重新登录！");
              return;
            }
            for(var i=0;i<self.bratDatas.length;i++){
              if(self.bratDatas[i].id=self.currentBratId){
                self.bratDatas[i].bratData=response.data.bratData
                if(response.data.newTerms!=null&&response.data.newTerms.length>0){
                  var forArr=response.data.newTerms;
                  self.bratDatas[i].newTerms=[];
                  for(var j=0;j<forArr.length;j++){
                      Object.keys(forArr[j]).forEach(function(key){
                        self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                    });
                  }  
                  self.currentBratData.newTerms=self.bratDatas[i].newTerms; 
                }else{
                  self.bratDatas[i].newTerms=[];
                  self.currentBratData.newTerms=[];
                }
              }
            }
            self.refreshData();
          });
        },
        //双击注解,然后弹出modal,modal上的按钮事件
        submitDeleteTerm(){
          var self=this;
          let formData=new FormData();
          formData.append("anId",self.currentBratId);
          formData.append("tag",self.selectedTerm.tag);
          //组织数据调用后台，刷新页面
          if(self.$refs.termDeleteDialog.addChecked){
            //删除标注，并删除新词
            formData.append("option","NEW_TERM");
            console.log("NEW_TERM");
          }else{
            //仅仅删除标注
            formData.append("option","ANNOTATION");
            console.log("ANNOTATION");
          }
          fetch('http://annotation.malgo.cn/annotation/delete.do',{
            method: 'POST',
            body:formData
            ,
            credentials:'include'
          })
          .then(function(response){
            return response.json()
          })
          .then(function(response){
            console.log("删除新词：")
            console.log(response)
            if(response.succ=="false"||response.succ==false){
              alert("请重新登录！");
              return;
            }
            for(var i=0;i<self.bratDatas.length ;i++){
              if(self.bratDatas[i].id==self.currentBratId){
                self.bratDatas[i].bratData=response.data.bratData
                if(self.$refs.termDeleteDialog.addChecked){
                  if(response.data.newTerms!=null&&response.data.newTerms.length>0){
                    var forArr=response.data.newTerms;
                    self.bratDatas[i].newTerms=[];
                    for(var j=0;j<forArr.length;j++){
                        Object.keys(forArr[j]).forEach(function(key){
                          self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                      });
                    }  
                    self.currentBratData.newTerms=self.bratDatas[i].newTerms; 
                  }else{
                    self.bratDatas[i].newTerms=[];
                    self.currentBratData.newTerms=[];
                  }
                }
              }
            }
            self.refreshData();
          });
        },
        //在父组件中定义该方法通过v-bind进行key/value的绑定，供子组件回调该方法
        submitTerm () {
          console.log('从表单中提交更改');
          var self=this;
          //保存当前bratId组下的所有新标注的term/type
          //组织form表单形式的请求数据，调用后台的updateSingle.do接口
          let formData=new FormData();
          formData.append("text",self.selectedTerm.term);
          formData.append("annotationType",self.selectedTerm.type);
          formData.append("startPosition",self.selectedTerm.startOffset);
          formData.append("endPosition",self.selectedTerm.endOffset);
          formData.append("anId",self.currentBratId);
          if(self.selectedTerm.isNewTerm){
            formData.append("option","NEW_TERM");//NEW_TERM,ANNOTATION 
          }else{
            formData.append("option","ANNOTATION");
          }      
          fetch('http://annotation.malgo.cn/annotation/add.do',{
            method: 'POST',
            body:formData
            ,
            credentials:'include'
          })
          .then(function(response){
            return response.json()
          })
          .then(function(response){
            if(response.succ=="false"||response.succ==false){
                alert("请重新登录！");
                return;
            }
            for(var i=0;i<self.bratDatas.length ;i++){
              if(self.bratDatas[i].id==self.currentBratId){
                self.bratDatas[i].bratData=response.data.bratData;
                if(response.data.newTerms!=null&&response.data.newTerms.length>0){
                  var forArr=response.data.newTerms;
                  self.bratDatas[i].newTerms=[];
                  for(var j=0;j<forArr.length;j++){
                    Object.keys(forArr[j]).forEach(function(key){
                      self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                    });
                  }
                  self.currentBratData.newTerms=self.bratDatas[i].newTerms;
                }
              }
            }
            self.refreshData()
          });
        },
        //在父组件中定义的方法，并注入到子组件中。供子组件调用，传入相应的参数，并带参数打开modal组件
        selectText (data, callback) {
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.startOffset=data.startOffset;
          self.selectedTerm.endOffset=data.endOffset;
          self.currentBratId=data.currentBratId;
          self.getTypesFromConfig();
          self.$refs.termDialog.open(self.selectedTerm);
          // self.submitTermCallBack=callback;
        },
        selectLabel (data) {
          // TODO 填数据
          // 新表单/可能删除某个标签
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.type=data.type;
          self.currentBratId=data.currentBratId;
          self.selectedTerm.tag=data.tag;
          //加一个判断，如果type类型后缀为"-unconfirmed",为自动标注,不允许弹出框
          if(!self.selectedTerm.type.endsWith('-unconfirmed')){
            self.$refs.termDeleteDialog.open(self.selectedTerm);
          }
        },
        commitAnnotation (id,bratId) {
          var self=this;
          console.log('提交第'+id+'个标注，其ID为：'+bratId)
          // 访问后台,提交 TODO
          // 返回数据中succ为true，则再次调用list.do接口，直接整个页面刷新
          let formFinish=new FormData();
          formFinish.append("id",bratId);
          fetch('http://annotation.malgo.cn/annotation/finish.do',{
            method:'POST',
            body:formFinish
            ,
            credentials:'include'
          })
          .then(function(response){
            return response.json()
          })
          .then(function(response){
              if(response.succ=="true"||response.succ==true){
                  alert("提交成功！");
                  self.refreshData()
              }else
              {
                alert("提交失败！");
              }
          });
        },
        reportSuspious (id,bratId) {
          var self=this;
          console.log('待定第'+id+'个标注,其ID为：'+bratId)
          // 访问后台,报告待定 TODO
          // 刷新获取新数据
          let formUnRecognize=new FormData();
          formUnRecognize.append("id",bratId);
          fetch('http://annotation.malgo.cn/annotation/unRecognize.do',{
            method:'POST',
            body:formUnRecognize
            ,
            credentials:'include'
          })
          .then(function(response){
            return response.json()
          })
          .then(function(response){
              if(response.succ=="true"||response.succ==true){
                  self.refreshData()
                  alert("待定成功！");
              }else{
                  alert("待定失败！");
              }
          });
        },
        refreshData () {
          var self=this;
          self.bratDatas=[];
          let formList=new FormData();
          formList.append('pageNum', "1");
          formList.append('pageSize', "10");
          fetch('http://annotation.malgo.cn/annotation/list.do',{
            method: 'POST',
            body:formList
            ,
            credentials:'include'
          })
          .then(function(response){
            return response.json()
          })
          .then(function(response){
            if(response.succ=="false"||response.succ==false){
              alert("请重新登录！");
              return;
            }
            self.bratDatas=response.data.dataList;
            console.log("刷新数据：")
            console.log(self.bratDatas)
            for(var i=0;i<self.bratDatas.length;i++){
              if(self.bratDatas[i].newTerms!=null&&self.bratDatas[i].newTerms.length>0){
                var forArr=self.bratDatas[i].newTerms;
                self.bratDatas[i].newTerms=[];
                for(var j=0;j<forArr.length;j++){
                  if(forArr[j]!=null){
                    var obj=Object.keys(forArr[j]).forEach(function(key){
                      self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                    });
                  }
                }
              }
            }
            if(self.bratDatas.length>0){
              self.loading=false;
            }
          })
        },
        //调用后台的list.do接口获取bratData标注数据，以及远程读取config.json文件的bratConfig内容
        getAllData () {
          var self=this
          self.loading=true;
          console.log("加载标注数据！");
          let formData=new FormData();
          formData.append('pageNum', "1");
          formData.append('pageSize', "10");
          fetch('http://annotation.malgo.cn/static/json/config.json',{
            credentials:'include'
          })
          .then(function(response){
            return response.json()
          })
          .then(function(response){
            self.bratConfig=response;
            fetch('http://annotation.malgo.cn/annotation/list.do',{
                credentials:'include',
                method: 'POST',
                body:formData
            })
            .then(function(response){
              return response.json()
            })
            .then(function(response){
              if(response.succ=="false"||response.succ==false){
                self.loading=false;
                alert("请重新登录！");
                return;
              }
              self.bratDatas=response.data.dataList;
              console.log(response.data.dataList)
              for(var i=0;i<self.bratDatas.length;i++){
                if(self.bratDatas[i].newTerms!=null&&self.bratDatas[i].newTerms.length>0){
                  var forArr=self.bratDatas[i].newTerms;
                  console.log(forArr);
                  self.bratDatas[i].newTerms=[];
                  for(var j=0;j<forArr.length;j++){
                    if(forArr[j]!=null){
                      var obj=Object.keys(forArr[j]).forEach(function(key){
                        self.bratDatas[i].newTerms.push(key+" - "+forArr[j][key]);
                      });
                    }
                  }
                } 
              }
              if(self.bratDatas.length>0){
                 self.loading=false;
              }
            });
          });
        },
        //从bratConfig集合中读取entity_types中的内容并存储在selectedTerm集合中的types数组中
        getTypesFromConfig () {
          if(this.selectedTerm.types==null||this.selectedTerm.types.length==0){
            var i=0,leni=this.bratConfig.entity_types.length;
            for(;i<leni;i++){
              if(!this.bratConfig.entity_types[i].type.endsWith('unconfirmed')){
                  this.selectedTerm.types.push({
                    label: this.bratConfig.entity_types[i].name,
                    value: this.bratConfig.entity_types[i].type,
                });
              }
              if(this.bratConfig.entity_types[i].children){
                var j=0,lenj=this.bratConfig.entity_types[i].children.length;
                for(;j<lenj;j++){
                  if(!this.bratConfig.entity_types[i].children[j].type.endsWith('unconfirmed')){
                    this.selectedTerm.types.push({
                        label: '|--' + this.bratConfig.entity_types[i].children[j].name,
                        value: this.bratConfig.entity_types[i].children[j].type,
                    });
                  }
                }
              }
            }
          }
        }   
      },
      components:{
        'malgo-brat':MalgoBrat,
        'modal-term-detail':ModalTermDetail,
        'modal-delete-term':ModalDeleteTerm
      }
    }
</script>


